import React from 'react';
import {CopyToClipboard} from "react-copy-to-clipboard";
import '../components/css/style-shrtcode.css';
import '../css/fonts.css';


const Shrtcode = (props) =>{

console.log(props)

return (
  <div>
    <div className="infoShorten">
      <div className="izq">
      <p>
         {props.originallink}
      </p>
      </div>
      <div className="der">
      <div className="linkcorto">
      <textarea type="text" value={props.shortlink}></textarea> 
      {/* <a id="linkcorto" href={props.shortlink}>{props.shortlink}</a>  */} 
      </div>  
      
      <div className="btncopy">
        <CopyToClipboard text={props.shortlink}>
            <button >Copy</button>
        </CopyToClipboard>
      </div>
      
      </div>
    </div>  
  </div>
    
)

}

export default Shrtcode;