import React from 'react'
import '../components/css/style-characteristics.css';
import '../css/fonts.css';


const Characteristics = ({icon, titulo, parrafo}) => (
     <div className="wrapper-card">
         <div className="Icon">
                <img src={icon} alt="icon" />
         </div>
         <div className="Info">
             <h6 id="Titulo">{titulo}</h6>
             <p id="Parrafo">{parrafo}</p>
         </div>
     </div>

);

export default Characteristics;