import React from 'react'
import '../components/css/style-shrtcode.css';
import '../css/fonts.css';


const Shrtcode = (props) =>{
return (
    <div className="contenedorshorten">
        <form onSubmit={props.getShorten}>
        <input type="text" name="link" placeholder="Shorten a link here..." className="inputlink"></input>
        <button className="btn-shorten">Shorten It!</button>
        </form>
   </div>
)

}

export default Shrtcode;